//使用vant组件库
import Vue from "vue";

import {
  NavBar,
  Form,
  Field,
  Button,
  Tabbar,
  TabbarItem,
  Icon,
  Tab,
  Tabs,
  Cell,
  List,
  PullRefresh,
  ActionSheet,
  Popup,
  Row,
  Col,
  Badge,
  Search,
  Image as VanImage,
  Divider,
  CellGroup,
  Tag,
  Dialog,
  DatetimePicker,
  Loading,
  Lazyload,
} from "vant";

Vue.use(Lazyload, {
  preLoad: 0.8, //预加载范围，高度默认0-1
  error:
    "https://img0.baidu.com/it/u=1750744693,4034691626&fm=253&fmt=auto&app=138&f=JPEG?w=360&h=240", //加载错误显示的图片
});
Vue.use(Loading);
Vue.use(DatetimePicker);
Vue.use(Dialog);
Vue.use(CellGroup);
Vue.use(Tag);
Vue.use(Divider);
Vue.use(VanImage);
Vue.use(Search);
Vue.use(Row);
Vue.use(Col);
Vue.use(Badge);
Vue.use(Popup);
Vue.use(ActionSheet);
Vue.use(PullRefresh);
Vue.use(List);
